  /*#__PURE__*/_createTextVNode(". It is a project of a German company of the same name. STXnext offered me the possibility to be a part of a small team of 3 developers, which goal was to show STXnext's ability to work on the development of this product, and thanks to that, convince the client to build with STXNext a bigger team or two, with more programmers, QAs and scrum master. ")
