const _hoisted_8 = /*#__PURE__*/_createStaticVNode("<div class=\"section-row\" data-v-58aa3a74><h3 class=\"section-title\" data-v-58aa3a74>Challenges</h3><h4 class=\"section-title\" data-v-58aa3a74>New frontend framework</h4><p data-v-58aa3a74> In 2015, I had a lot of experience with Django&amp;Python, only some experience with jQuery and BackboneJS, but no prior experience with Angular JS. That was a challenge of its own for me. </p><p data-v-58aa3a74> Fortunately, our client and our team were long-term oriented, so we were embracing opportunities to learn new things if particular tasks were not critical. Everyone in a group was contributing the most in areas where he felt the strongest, but we created a culture of working on things outside of our area of expertise. That helped everyone in the team learn a lot of new things. </p><h4 class=\"section-title\" data-v-58aa3a74> Bootstrapping new team for a client, new code base, no know-how </h4><p data-v-58aa3a74> Because we formed a new independent team, we started with a very narrow understanding of the codebase, with limited know-how about the whole project. We had contact with all in-house developers and a Product Owner from Seerene. As you can imagine, it was more challenging to learn about all the nitty-gritty details without having people with the knowledge inside the same room. To deal with that, we needed to spend a lot of time on talks via slack or video calls. </p><p data-v-58aa3a74> The critical part was to focus on asking the right questions, to understand things that were truly necessary to develop scoped tasks. </p><p data-v-58aa3a74> I believe the key to good performance was Planning and Estimation meetings, during which we together dissected all problems while trying to identify risks. </p><h4 class=\"section-title\" data-v-58aa3a74> Lack of infrastructure on our end; Custom CI build-system </h4><p data-v-58aa3a74> To be able to work on a product efficiently, our team needed to also set up a few production-like instances of the project: our local production, staging, few test instances, which later were used by our QAs. </p><p data-v-58aa3a74> Earlier there were attempts to do those deploys with some basic bash scripts. Despite the simplicity, our team quickly realize that this solution was not flexible enough and wasn&#39;t scaling well. </p><p data-v-58aa3a74> At some point I started working on proper containerization with Docker of the whole thing, creating with Jenkins the building pipeline (CI), responsible for running all unit and E2E tests, but also capable of building new tests instances on-demand with just a few clicks. One of the most important features of that build system (which speeds up the work of our QAs significantly), was the ability to clone prepared databases between instances, with just a few clicks. </p><p data-v-58aa3a74> All instances build on-demand were also always automatically dynamically set up with their separate subdomain and with the support of HTTPS &amp; SSL certificates. </p><p data-v-58aa3a74> I&#39;ve built that build system on my own from scratch using <a href=\"https://github.com/cookiecutter/Cookiecutter\" data-v-58aa3a74>Cookiecuttter</a> library and its Jinja template language. That helped keep the whole thing very simple, yet powerful and robust. </p><p data-v-58aa3a74> Before I left STXNext and this project, I&#39;ve successfully transferred all my knowledge about this build system to other developers, which I believe also proved it was easy to understand also from the code&#39;s perspective. </p><h4 class=\"section-title\" data-v-58aa3a74>Optimizing the speed of database queries</h4><p data-v-58aa3a74> To be precise, it was not something on which I&#39;ve worked personally, however, my team did. I&#39;ve decided to mention this anyway, because of the fact how this task was handled. Two guys from our team, which were the most into databases... decided to work on this together. Pair programming, analyzing, refactoring took them over a month, however, the result was stunning. Without any major hiccups, they optimized the execution time of business-critical queries, from dozens of minutes to hundreds of milliseconds. </p><p data-v-58aa3a74> While working in this team, I&#39;ve learned how powerful pair-programming can be as a tool to develop quickly important parts of code with high quality in mind. Additionally, this speeds up the learning process of participating developers tremendously. I&#39;ve personally used that technique many times later within that project, on a smaller scale. Since then, I&#39;m a huge fan of pair programming! (Which BTW, what my other project proved to me... can be successfully done also while working remotely!) </p></div>", 1)
