const _hoisted_17 = /*#__PURE__*/_createStaticVNode("<p> If you want to be a business owner, then you should be at the same time very skeptical and very optimistic. You should be prepared for the worse, but you should hope for the best. Skepticism is important because you should test your every assumption that you can make money on something, but optimism is needed to find a way to prove that your skepticism was <strong>in this particular case</strong> unnecessary. </p><p> I can recommend you an article <em>&quot;<a href=\"http://thenextweb.com/dd/2014/11/12/15-ways-test-minimum-viable-product/#gref\" rel=\"noopener\" title=\"This link will take you away from steemit.com\" class=\"keychainify-checked steem-keychain-checked\">15 ways to test your minimum viable product</a>&quot;</em>. There are 8 items from article which could be used in an MVB instead of MVP: </p><ul><li>Customer interviews</li><li>Landing Pages</li><li>Ad Campaigns</li><li>Fundraising</li><li>Explainer videos</li><li>Manual-first (aka Wizard of Oz) MVP</li><li>Digital &amp; Paper prototypes</li><li>Pre-order pages</li></ul><p> Those techniques can save you months of unnecessary work and tons of money. If you want to build a business, you should first validate your idea with at least one technique described there. </p><h3>Minimum Viable <strong>Business</strong> - first, MVP - later</h3><p> MVB is a new term. For me it is helpful because it helps me split the idea of building a company into: &quot;research &amp; planning&quot; - MVB and &quot;actual implementation&quot; - MVP. I am a software developer, and most of the projects I&#39;ve started because I wanted to learn something about programming in the process of doing that - how to implement this functionality, how to use that library, etc. </p><p> It would be a great outcome, if one of my projects just took off, and became very successful. But this would assume, that I had really great idea in first place, and that I had implemented this flawlessly - sadly this scenario is very unlikely to happen. You don&#39;t know what the market wants. </p><p><strong>Businesses are not born <em>automagically</em>.</strong> If you focus on building a product, you can build one, even a great one... but this does not necessarily mean that you will be able to earn anything from that. If at some point you will decide to create a business, then you have to start thinking as a business owner, not like a developer, who loves to learn new things. </p><p> If you know the product which you want to build, you will need funding or a business to be able to exist and grow. Then before building a product you should focus on building a business. Don&#39;t get me wrong - this doesn&#39;t mean that you have to create a company. You should focus on validation, whether your business actually has a chance to be a successful business or at least will be able to support the development of a solution which your product is supposed to solve. </p><h3 class=\"section-title\"> Being sure that there is demand for a product like yours </h3>", 10)
