const _hoisted_30 = /*#__PURE__*/_createElementVNode("p", null, " In the 90's a lot of companies were sure that Virtual Reality (VR) is the future. The giants like Nintendo, Atari, Sega invested tons of money in VR because their competitors were doing the same. No one wanted to be left behind. Even right now, we are not 100% sure that there is demand for VR, which companies can offer to customers. ", -1)
