const _hoisted_43 = /*#__PURE__*/_createTextVNode(" MVB/MVP tests are designed not just to answer technical questions about the product, but also to test fundamental business hypotheses about the viability of the market it exists in. ")
const _hoisted_44 = /*#__PURE__*/_createStaticVNode("<p> MVB as a cure for the &quot;no market need&quot; problem should be like a bait - it should check how many users your product will be able to attract. Not only that, you also have to be sure that those users are your target (with specific needs and funds that they are willing to spent on things that your product can provide). </p><h4 class=\"section-subtitle\">Ran Out of Cash</h4><p> This also happened to us. Right now I think that <em>running out of cash</em> is always an <em>outcome, not a reason</em> itself. </p><p><em>I wanted to build a product for myself, so I started only with my own savings and my own time.</em> I didn&#39;t validate whether anyone would be interested in funding my work in advance (investors, crowdfunding, <a href=\"http://lexicon.ft.com/Term?term=3-Fs\" rel=\"noopener\" title=\"This link will take you away from steemit.com\" class=\"keychainify-checked steem-keychain-checked\">FFF</a>) </p><p><em>I consider myself as a customer, so I decided to build a few features, because I thought that they were cool.</em> I wasted too much time on the development of things that were not crucial, and spent too little time on features that could help us monetize a service. </p><p><em>I spent too much time and money which was required to implement some features that should not have even existed in the first version of our product.</em> I decided that to outperform our competitors we were going to integrate more bookstores than others. Because of that we needed to spend: </p><ul><li>much more time on integration of those bookstores</li><li>much more time on performance optimization</li><li> much more money on servers that were needed to handle updates from those bookstores </li></ul><h4 class=\"section-subtitle\"> Get Outcompeted, Poor product, Ignore Customers </h4><p> If your startup was outcompeted, probably you did something wrong. Of course, you can always say that your opponent had the &quot;first move advantage&quot;, but that would be an excuse only in case that either of you did not make any mistakes. </p><p> You should not try to build a perfect product just at the beginning. If this is even possible, for sure it is too expensive. Most mistakes can be corrected, so success at the end depends on speed, performance, and good research about things which are not perfect. In all this cases to make progress you need to work closely with your customer. You should make your product iterations as short as possible, and after each iteration, you should check feedback from your users. </p><p> This means, that you should have a separate MVP of each feature! Even if you think that you already know, that specific feature is needed and desired by customers, you should first implement a small part of it - 20%, and at the same time try to bring 80% of feature value to the table. </p><h3 class=\"section-title\">MVP is different for each industry</h3><p> I think one explanation for why there is so much confusion with MVP is that it looks quite different for different companies. </p>", 13)
